<link href="https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap" rel="stylesheet">

<script>
	import Header from './Header.svelte';
	import HomeWide from './HomeWide.svelte';
	import HomeNarrow from './HomeNarrow.svelte';

	let src = 'portrait.jpg';
	let winWidth;
	let winHeight;
	let resizeInProgress = false;
	let timeout = null;
	const mobileOperatingSystem = getMobileOperatingSystem();

	// https://stackoverflow.com/a/21742107/6586631
	function getMobileOperatingSystem() {
		let userAgent = navigator.userAgent || navigator.vendor || window.opera;
		// Windows Phone must come first because its UA also contains "Android"
		if (/windows phone/i.test(userAgent)) {
			return "windows_phone";
		}
		if (/android/i.test(userAgent)) {
			return "android";
		}
		if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
			return "ios";
		}
		return "unknown";
	}

	window.addEventListener("resize", updateWindowSize);
	function updateWindowSize() {
		resizeInProgress = true;
		clearTimeout(timeout);
		timeout = setTimeout(() => { resizeInProgress = false; }, 500);
	}
</script>

<svelte:window bind:innerWidth={winWidth} bind:innerHeight={winHeight}/>

<style>
	.outer {
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
	.container {
		/* font-family: 'Montserrat', sans-serif; */
		font-family: 'Roboto', sans-serif;
		/* font-style: italic; */
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: absolute;
		height: auto;
		bottom: 0;
		top: 0;
		padding: 20px;
		max-width: 1280px;
	}
	#overlay {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: absolute;
		bottom: 0;
		top: 0;
		left: 0;
		right: 0;
		color: white;
		background-color: rgba(0, 0, 0, 0.75);
		z-index: 2;
	}
	.box {
		border: 1px solid white;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		text-align: center;
		font-weight: 300;
		font-size: 18px;
		margin: 10px;
		background-color: rgba(0,0,100,0.5);
		background: 
			linear-gradient(
				to top left,
				rgba(0,0,0,0) 0%,
				rgba(0,0,0,0) calc(50% - 1px),
				white 50%,
				rgba(0,0,0,0) calc(50% + 1px),
				rgba(0,0,0,0) 100%
			),
			linear-gradient(
				to top right,
				rgba(0,0,0,0) 0%,
				rgba(0,0,0,0) calc(50% - 0.8px),
				white 50%,
				rgba(0,0,0,0) calc(50% + 0.8px),
				rgba(0,0,0,0) 100%
			);
	}
</style>

<div class="outer">
<div class="container">
	<Header/>
	{#if winWidth > 710}
		 <HomeWide/>
	{:else}
		 <HomeNarrow/>
	{/if}
	<div/> <!-- empty div to center Home component via flex -->
</div>
</div>

{#if mobileOperatingSystem == 'unknown' && resizeInProgress}
	<div id="overlay">
		<div class="box">
			{winWidth}x{winHeight}
			<div/>
			<div/>
			<div/>
			{winWidth > 710 ? 'desktop' : 'mobile'}
		</div>
	</div>
{/if}
