<script>
	const first = 'christoph';
    const last = 'schwizer';
    let dropdownArrow = 'down-outline.png';
    function showProjects() {
        document.getElementById("projectsDropdown").classList.toggle("hidden");
        document.getElementById("dropdownArrow").classList.toggle("unpressed");
    }
    function onDropdownMouseOver() {
        dropdownArrow = 'down-filled.png';
    }
    function onDropdownMouseOut() {
        dropdownArrow = 'down-outline.png';
    }
</script>

<style>
    .container {
        display: flex;
        justify-content: space-between;
        font-weight: 300;
        font-size: 14px;
        letter-spacing: 0.22em;
    }
    .last {
        font-weight: 500;
    }
    .dropdown {
        display: inline-block;
    }
    .dropdownButton {
        letter-spacing: 0.2em;
        user-select: none;
    }
    .dropdownButton:hover {
        cursor: pointer;
    }
    .dropdownContent {
        background-color: rgba(255, 255, 255, 0.5);
        border-top: 1px solid black;
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 20px;
        overflow: auto;
        z-index: 1;
        margin-top: 10px;
        transform: scaleY(1);
        transition-property: transform;
        transform-origin: top;
        transition-duration: 200ms;
    }
    .dropdownContent.hidden {
        transform: scaleY(0);
    }
    .dropdownContent a {
        color: black;
        padding: 10px;
        letter-spacing: 0.1em;
        border: 0;
    }
    .dropdownContent a:hover {
        color: white;
        background-color: black;
        text-decoration: none;
        padding-left: 10px;
        border: 0;
    }
    .dropdownArrow {
        /* this is the pressed state */
        height: 10px;
        width: 10px;
        transition-property: transform;
        transition-duration: 300ms;
        transform: rotate(-180deg);
    }
    .dropdownArrow.unpressed {
        transform: rotate(0deg);
    }
</style>

<div class="container">
    <div class="logo">
        <span class="first">{first}</span><span class="last">{last}</span>
    </div>

    <div class="dropdown">
        <div class="dropdownButton" on:click={showProjects} on:mouseover={onDropdownMouseOver} on:mouseout={onDropdownMouseOut}>
            <img id="dropdownArrow" class="dropdownArrow unpressed" src="{dropdownArrow}" alt="arrow"/> projects
        </div>
        <div id="projectsDropdown" class="dropdownContent hidden">
            <a href="https://github.com/chrisly-bear/PEBRApp">PEBRApp</a>
            <a href="https://christophschwizer.ch/sunrise-usage">Sunrise Usage</a>
            <a href="https://github.com/chrisly-bear/time-machine-progress">Time Machine Progress</a>
        </div>
    </div>
</div>